import React, { useState, useEffect } from 'react';
import { db } from '../firebaseConfig';
import { collection, addDoc, getDocs } from 'firebase/firestore';
import { useNavigate } from 'react-router-dom';

const Add2FA = () => {
  const [title, setTitle] = useState('');
  const [secret, setSecret] = useState('');
  const [priority, setPriority] = useState(1000); // Mc nh l 1000
  const [error, setError] = useState('');
  const navigate = useNavigate();

  // Ly gi tr priority thp nht khi ti trang
  useEffect(() => {
    const fetchPriorities = async () => {
      const querySnapshot = await getDocs(collection(db, '2fa-codes'));
      const fetchedCodes = querySnapshot.docs.map((doc) => ({ ...doc.data(), id: doc.id }));

      if (fetchedCodes.length === 0) {
        setPriority(1000); // Nu danh sch rng, mc nh priority l 1000
      } else {
        // Ly gi tr priority nh nht v tr i 20
        const minPriority = Math.min(...fetchedCodes.map((code) => code.priority));
        const newPriority = Math.max(minPriority - 20, 0); // Khng cho php gi tr m
        setPriority(newPriority);
      }
    };

    fetchPriorities();
  }, []);

  const handleAdd = async () => {
    if (!title || !secret) {
      setError('Vui lng nhp y  thng tin');
      return;
    }
    try {
      await addDoc(collection(db, '2fa-codes'), {
        title,
        secret,
        priority: Number(priority),
      });
      setTitle('');
      setSecret('');
      setPriority(1000); // t li gi tr priority mc nh sau khi thm m
      navigate('/home'); // Chuyn hng v trang home sau khi thm m
    } catch (e) {
      setError('Li khi thm m: ' + e.message);
    }
  };

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-6">
          <div className="card shadow-lg">
            <div className="card-header bg-primary text-white">
              <h4 className="mb-0">Thm m 2FA</h4>
            </div>
            <div className="card-body">
              {error && <div className="alert alert-danger">{error}</div>}
              <div className="mb-3">
                <label htmlFor="title" className="form-label">Tiu </label>
                <input
                  type="text"
                  id="title"
                  className="form-control"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  placeholder="Nhp tiu "
                />
              </div>
              <div className="mb-3">
                <label htmlFor="secret" className="form-label">Kha b mt</label>
                <input
                  type="text"
                  id="secret"
                  className="form-control"
                  value={secret}
                  onChange={(e) => setSecret(e.target.value)}
                  placeholder="Nhp kha b mt"
                />
              </div>
              <div className="mb-3">
                <label htmlFor="priority" className="form-label"> u tin</label>
                <input
                  type="number"
                  id="priority"
                  className="form-control"
                  value={priority}
                  onChange={(e) => setPriority(e.target.value)} // Cho php ngi dng chnh sa priority
                  placeholder="Nhp  u tin"
                />
              </div>
              <div className="d-grid">
                <button className="btn btn-primary" onClick={handleAdd}>
                  <i className="fas fa-plus-circle me-2"></i>Thm m
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Add2FA;
