import React, { useState, useEffect } from 'react';
import { auth } from '../firebaseConfig';
import { signInWithEmailAndPassword, signOut, setPersistence, browserLocalPersistence } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  // Kim tra trng thi ng nhp
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((currentUser) => {
      setUser(currentUser);
    });
    return () => unsubscribe();
  }, []);

  // X l ng nhp
  const handleLogin = (e) => {
    e.preventDefault();
  
    // Thit lp ch  lu tr  gi trng thi ng nhp khi ti li trang
    setPersistence(auth, browserLocalPersistence)
      .then(() => {
        return signInWithEmailAndPassword(auth, email, password);
      })
      .then(() => {
        navigate('/home'); // Chuyn hng sau khi ng nhp
      })
      .catch((error) => {
        setError('ng nhp tht bi: ' + error.message);
      });
  };
  

  // X l ng xut
  const handleLogout = () => {
    signOut(auth)
      .then(() => {
        setUser(null); // Reset li user khi ng xut
        navigate('/'); // Chuyn hng v trang ng nhp sau khi ng xut
      })
      .catch((error) => {
        console.error('ng xut tht bi: ', error);
      });
  };

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-6">
          <h2>ng nhp</h2>
          <form onSubmit={handleLogin}>
            <div className="mb-3">
              <label htmlFor="email" className="form-label">Email</label>
              <input
                type="email"
                className="form-control"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Nhp email"
              />
            </div>
            <div className="mb-3">
              <label htmlFor="password" className="form-label">Mt khu</label>
              <input
                type="password"
                className="form-control"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Nhp mt khu"
              />
            </div>
            <button type="submit" className="btn btn-primary">ng nhp</button>
          </form>
          {error && <p className="text-danger mt-2">{error}</p>}
          
          {/* Nt ng xut ch hin th khi ngi dng  ng nhp */}
          {user && (
            <button onClick={handleLogout} className="btn btn-secondary mt-3">
              ng xut
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default Login;
