import React, { useState, useEffect } from 'react';
import { db } from '../firebaseConfig';
import { collection, getDocs, deleteDoc, doc } from 'firebase/firestore';
import { Link } from 'react-router-dom';
import { TOTP } from 'otpauth'; // Import TOTP t otpauth
import { CopyToClipboard } from 'react-copy-to-clipboard';
import './Home.css';

const Home = () => {
  const [codes, setCodes] = useState([]);
  const [copied, setCopied] = useState(null); //  theo di trng thi sao chp
  const [codeToDelete, setCodeToDelete] = useState(null); // M cn xa
  const [showDeletePopup, setShowDeletePopup] = useState(false); // Hin th popup xc nhn
  const [timeLeft, setTimeLeft] = useState(30); // Thi gian m ngc

  // Ly danh sch m 2FA
  const fetch2FACodes = async () => {
    const querySnapshot = await getDocs(collection(db, '2fa-codes'));
    let fetchedCodes = querySnapshot.docs.map((doc) => ({ ...doc.data(), id: doc.id }));

    // Sp xp danh sch theo priority (u tin tng dn)
    fetchedCodes = fetchedCodes.sort((a, b) => a.priority - b.priority);

    setCodes(fetchedCodes);
  };

  // To m 2FA t secret key s dng otpauth
  const generateOTP = (secret) => {
    const totp = new TOTP({
      secret: secret,  // Secret key ca m 2FA (cn  nh dng Base32)
      digits: 6,       // S ch s ca m 2FA
      period: 30       // Thi gian chu k l 30 giy
    });

    // To m OTP
    return totp.generate();
  };

  // Cp nht thi gian m ngc
  const calculateTimeLeft = () => {
    const epoch = Math.floor(Date.now() / 1000); // Ly thi gian hin ti theo giy
    const timeRemaining = 30 - (epoch % 30); // Phn d ca thi gian trong chu k 30 giy
    return timeRemaining;
  };

  useEffect(() => {
    const interval = setInterval(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000); // Cp nht mi giy

    return () => clearInterval(interval); // Hy b b m thi gian khi component unmount
  }, []);

  // Hm x l sao chp
  const handleCopy = (codeId) => {
    setCopied(codeId); // t trng thi  sao chp
    setTimeout(() => setCopied(null), 2000); // Sau 2 giy, reset li trng thi
  };

  // Hm hin th popup xc nhn xa
  const confirmDelete = (codeId) => {
    setCodeToDelete(codeId);
    setShowDeletePopup(true); // Hin th popup
  };

  // Hm x l xa
  const handleDelete = async () => {
    try {
      await deleteDoc(doc(db, '2fa-codes', codeToDelete));
      setShowDeletePopup(false); // ng popup sau khi xa
      fetch2FACodes(); // Ti li danh sch sau khi xa
    } catch (error) {
      console.error("Li khi xa: ", error);
    }
  };

  useEffect(() => {
    fetch2FACodes(); // Ly danh sch m 2FA khi trang c load ln u
  }, []);

  return (
    <div className="container mt-5">
      <h2>Danh sch m 2FA</h2>
      <Link to="/add" className="btn btn-success mb-3">Thm m 2FA mi</Link>
      <ul className="list-group">
        {codes.map((code) => {
          const otp = generateOTP(code.secret); // To m OTP

          return (
            <li key={code.id} className="list-group-item d-flex justify-content-between align-items-center">
              <div>
                <p><strong>Title:</strong> {code.title}</p>
                <p>
                  <strong>2FA Code:  </strong>
                  <CopyToClipboard text={otp} onCopy={() => handleCopy(code.id)}>
                    <span style={{ cursor: 'pointer', color: 'red' }} title="Click  sao chp">
                      {otp} <i className="fa-solid fa-copy" style={{ marginLeft: '5px' }}></i>
                    </span>
                  </CopyToClipboard>

                  {/* Thi gian m ngc thit k li */}
                  <span className="badge countdown-timer" style={{ marginLeft: '10px' }}>
                    {timeLeft} giy
                  </span>
                </p>
                <p><strong>Priority:</strong> {code.priority}</p>
              </div>

              {/* Thng bo khi sao chp */}
              {
                copied === code.id && (
                  <div style={popupStyle}>
                    <span> sao chp!</span>
                  </div>
                )
              }

              <button className="btn btn-danger" onClick={() => confirmDelete(code.id)}>Xa</button>
            </li>
          );
        })}
      </ul>

      {/* Popup xc nhn xa */}
      {
        showDeletePopup && (
          <div className="popup">
            <div className="popup-content">
              <h5>Bn c chc chn mun xa?</h5>
              <button className="btn btn-danger me-3" onClick={handleDelete}>Xa</button>
              <button className="btn btn-secondary" onClick={() => setShowDeletePopup(false)}>Khng</button>
            </div>
          </div>
        )
      }
    </div >
  );
};

const popupStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  backgroundColor: '#d4edda',
  padding: '5px 10px',
  borderRadius: '5px',
  color: '#155724',
  fontSize: '12px',
  boxShadow: '0px 4px 6px rgba(0, 0, 0, 0.1)',
  zIndex: 1,
};

export default Home;
