import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { auth } from '../firebaseConfig';
import { useNavigate } from 'react-router-dom';
import './Sidebar.css'; // Import CSS cho sidebar

const Sidebar = () => {
  const navigate = useNavigate();
  const location = useLocation(); // Ly URL hin ti
  const [isOpen, setIsOpen] = useState(false); // Trng thi m/ng ca sidebar

  // Hm x l ng xut
  const handleLogout = () => {
    auth.signOut()
      .then(() => {
        navigate('/');
      })
      .catch((error) => {
        console.error('Li khi ng xut: ', error);
      });
  };

  // Hm x l m/ng sidebar
  const toggleSidebar = () => {
    setIsOpen(!isOpen);
  };

  // Hm  kim tra xem ng dn hin ti c trng vi ng dn ca mt nt hay khng
  const isActive = (path) => location.pathname === path;

  return (
    <>
      {/* Nt m sidebar trn thit b nh */}
      <button
        className="btn btn-light d-md-none"
        style={{ position: 'fixed', top: '10px', left: '10px', zIndex: '999' }}
        onClick={toggleSidebar}
      >
        <i className="fas fa-bars"></i> {/* Biu tng hamburger t Font Awesome */}
      </button>

      {/* Sidebar */}
      <div
        className={`sidebar bg-light d-flex flex-column ${isOpen ? 'sidebar-open' : 'sidebar-closed'} d-md-block`}
      >
        <h4 className="p-3">Qun l 2FA</h4>
        <ul className="nav nav-pills flex-column mb-auto">
          <li className="nav-item">
            <Link
              to="/home"
              className={`nav-link ${isActive('/home') ? 'active-link' : ''}`} // Bi m khi active
              onClick={toggleSidebar} // ng sidebar khi click
            >
              <i className="fas fa-home"></i> Home
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/add"
              className={`nav-link ${isActive('/add') ? 'active-link' : ''}`} // Bi m khi active
              onClick={toggleSidebar} // ng sidebar khi click
            >
              <i className="fas fa-plus"></i> Thm m 2FA
            </Link>
          </li>
        </ul>
        <hr />

        {/* Hin th email ngi dng */}
        <div className="email-container p-3">
          <span style={{ fontSize: '14px', fontWeight: 'bold', color: '#555' }}>
            {auth.currentUser?.email}
          </span>
        </div>

        {/* Nt logout di chuyn xung cui */}
        <button className="btn btn-danger mt-auto mb-3 p-2" onClick={handleLogout} style={{ width: '90%', marginLeft: 'auto', marginRight: 'auto' }}>
          <i className="fas fa-sign-out-alt"></i> Logout
        </button>
      </div>

      {/* Nn m khi sidebar m trn thit b nh */}
      {isOpen && <div className="overlay" onClick={toggleSidebar} />}
    </>
  );
};

export default Sidebar;
